#!/usr/bin/env node

/**
 * WordPress Theme Build Script with Timestamp
 * 
 * „Éì„É´„ÉâÁµêÊûú„Å´„Çø„Ç§„É†„Çπ„Çø„É≥„Éó„Çí‰ªò‰∏é„Åó„Å¶dist„Éá„Ç£„É¨„ÇØ„Éà„É™„Å´Âá∫Âäõ
 */

const fs = require('fs-extra');
const path = require('path');
const archiver = require('archiver');
const { execSync } = require('child_process');

// „Çø„Ç§„É†„Çπ„Çø„É≥„ÉóÁîüÊàêÔºàYYYYMMDD-HHMMSSÂΩ¢ÂºèÔºâ
function getTimestamp() {
    const now = new Date();
    const year = now.getFullYear();
    const month = String(now.getMonth() + 1).padStart(2, '0');
    const day = String(now.getDate()).padStart(2, '0');
    const hours = String(now.getHours()).padStart(2, '0');
    const minutes = String(now.getMinutes()).padStart(2, '0');
    const seconds = String(now.getSeconds()).padStart(2, '0');
    
    return `${year}${month}${day}-${hours}${minutes}${seconds}`;
}

// „Éì„É´„ÉâË®≠ÂÆö
const config = {
    sourceDir: path.join(__dirname, '../wordpress/lightningtalk-child'),
    distDir: path.join(__dirname, '../dist'),
    tempDir: path.join(__dirname, '../.tmp/build'),
    themeName: 'lightningtalk-child',
    timestamp: getTimestamp()
};

// „Éì„É´„ÉâÊÉÖÂ†±
const buildInfo = {
    version: '1.0.0',
    buildDate: new Date().toISOString(),
    timestamp: config.timestamp,
    environment: process.env.NODE_ENV || 'production'
};

// Èô§Â§ñ„Éï„Ç°„Ç§„É´„Éª„Éá„Ç£„É¨„ÇØ„Éà„É™
const excludePatterns = [
    'node_modules',
    '.git',
    '.gitignore',
    '*.log',
    '.DS_Store',
    'Thumbs.db',
    '.env',
    '.env.local',
    '*.map',
    'src/',
    '*.scss',
    '*.ts',
    '*.tsx',
    'package.json',
    'package-lock.json',
    'README.md',
    'webpack.config.js',
    'gulpfile.js'
];

async function ensureDirectories() {
    console.log('üìÅ „Éá„Ç£„É¨„ÇØ„Éà„É™„ÇíÊ∫ñÂÇô„Åó„Å¶„ÅÑ„Åæ„Åô...');
    
    // dist„Éá„Ç£„É¨„ÇØ„Éà„É™ÊßãÈÄ†„Çí‰ΩúÊàê
    const dirs = [
        config.distDir,
        path.join(config.distDir, 'themes'),
        path.join(config.distDir, 'builds'),
        path.join(config.distDir, 'archives'),
        config.tempDir
    ];
    
    for (const dir of dirs) {
        await fs.ensureDir(dir);
    }
}

async function cleanTempDirectory() {
    console.log('üßπ ‰∏ÄÊôÇ„Éá„Ç£„É¨„ÇØ„Éà„É™„Çí„ÇØ„É™„Éº„É≥„Ç¢„ÉÉ„Éó„Åó„Å¶„ÅÑ„Åæ„Åô...');
    await fs.remove(config.tempDir);
    await fs.ensureDir(config.tempDir);
}

async function copyThemeFiles() {
    console.log('üìã „ÉÜ„Éº„Éû„Éï„Ç°„Ç§„É´„Çí„Ç≥„Éî„Éº„Åó„Å¶„ÅÑ„Åæ„Åô...');
    
    const targetDir = path.join(config.tempDir, config.themeName);
    await fs.ensureDir(targetDir);
    
    // „ÇΩ„Éº„Çπ„Éá„Ç£„É¨„ÇØ„Éà„É™„ÅÆÂ≠òÂú®Á¢∫Ë™ç
    if (!await fs.pathExists(config.sourceDir)) {
        throw new Error(`„ÇΩ„Éº„Çπ„Éá„Ç£„É¨„ÇØ„Éà„É™„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì: ${config.sourceDir}`);
    }
    
    // „Éï„Ç°„Ç§„É´„Çí„Ç≥„Éî„ÉºÔºàÈô§Â§ñ„Éë„Çø„Éº„É≥„ÇíËÄÉÊÖÆÔºâ
    const files = await fs.readdir(config.sourceDir);
    
    for (const file of files) {
        const shouldExclude = excludePatterns.some(pattern => {
            if (pattern.includes('*')) {
                const regex = new RegExp(pattern.replace('*', '.*'));
                return regex.test(file);
            }
            return file === pattern;
        });
        
        if (!shouldExclude) {
            const sourcePath = path.join(config.sourceDir, file);
            const targetPath = path.join(targetDir, file);
            await fs.copy(sourcePath, targetPath);
        }
    }
    
    // „Éì„É´„ÉâÊÉÖÂ†±„Éï„Ç°„Ç§„É´„ÇíËøΩÂä†
    const buildInfoPath = path.join(targetDir, 'build-info.json');
    await fs.writeJson(buildInfoPath, buildInfo, { spaces: 2 });
}

async function runAssetBuild() {
    console.log('üî® „Ç¢„Çª„ÉÉ„Éà„Çí„Éì„É´„Éâ„Åó„Å¶„ÅÑ„Åæ„Åô...');
    
    try {
        // npm run build„ÅåÂ≠òÂú®„Åô„ÇãÂ†¥Âêà„ÅØÂÆüË°å
        if (await fs.pathExists(path.join(config.sourceDir, 'package.json'))) {
            execSync('npm run build', {
                cwd: config.sourceDir,
                stdio: 'inherit'
            });
        }
    } catch (error) {
        console.warn('‚ö†Ô∏è  „Ç¢„Çª„ÉÉ„Éà„Éì„É´„Éâ„Çí„Çπ„Ç≠„ÉÉ„Éó„Åó„Åæ„Åó„Åü:', error.message);
    }
}

async function createZipArchive() {
    console.log('üì¶ ZIP„Ç¢„Éº„Ç´„Ç§„Éñ„Çí‰ΩúÊàê„Åó„Å¶„ÅÑ„Åæ„Åô...');
    
    const zipFileName = `${config.themeName}_${config.timestamp}.zip`;
    const zipPath = path.join(config.distDir, 'themes', zipFileName);
    
    return new Promise((resolve, reject) => {
        const output = fs.createWriteStream(zipPath);
        const archive = archiver('zip', {
            zlib: { level: 9 } // ÊúÄÈ´òÂúßÁ∏Æ„É¨„Éô„É´
        });
        
        output.on('close', () => {
            const size = (archive.pointer() / 1024).toFixed(2);
            console.log(`‚úÖ ZIP„Éï„Ç°„Ç§„É´‰ΩúÊàêÂÆå‰∫Ü: ${zipFileName} (${size} KB)`);
            resolve({ fileName: zipFileName, path: zipPath, size: archive.pointer() });
        });
        
        archive.on('error', (err) => {
            reject(err);
        });
        
        archive.pipe(output);
        archive.directory(config.tempDir, false);
        archive.finalize();
    });
}

async function createBuildManifest(zipInfo) {
    console.log('üìù „Éì„É´„Éâ„Éû„Éã„Éï„Çß„Çπ„Éà„Çí‰ΩúÊàê„Åó„Å¶„ÅÑ„Åæ„Åô...');
    
    const manifest = {
        build: {
            ...buildInfo,
            files: {
                theme: zipInfo.fileName,
                size: zipInfo.size,
                sizeHuman: `${(zipInfo.size / 1024).toFixed(2)} KB`
            }
        },
        theme: {
            name: config.themeName,
            version: buildInfo.version,
            parent: 'cocoon',
            description: 'Lightning Talk Event Management Child Theme'
        },
        deployment: {
            targetUrl: process.env.WP_SITE_URL || 'https://xn--6wym69a.com',
            targetPath: `/wp-content/themes/${config.themeName}/`
        },
        checksums: {
            md5: await generateChecksum(zipInfo.path, 'md5'),
            sha256: await generateChecksum(zipInfo.path, 'sha256')
        }
    };
    
    const manifestPath = path.join(config.distDir, 'builds', `build-manifest_${config.timestamp}.json`);
    await fs.writeJson(manifestPath, manifest, { spaces: 2 });
    
    // ÊúÄÊñ∞„Éì„É´„Éâ„Å∏„ÅÆ„Ç∑„É≥„Éú„É™„ÉÉ„ÇØ„É™„É≥„ÇØ„Çí‰ΩúÊàêÔºàWindows„Åß„Å™„ÅÑÂ†¥ÂêàÔºâ
    if (process.platform !== 'win32') {
        const latestLink = path.join(config.distDir, 'themes', `${config.themeName}_latest.zip`);
        const latestManifest = path.join(config.distDir, 'builds', 'build-manifest_latest.json');
        
        try {
            await fs.remove(latestLink);
            await fs.remove(latestManifest);
            await fs.symlink(zipInfo.path, latestLink);
            await fs.symlink(manifestPath, latestManifest);
        } catch (error) {
            console.warn('‚ö†Ô∏è  „Ç∑„É≥„Éú„É™„ÉÉ„ÇØ„É™„É≥„ÇØ„ÅÆ‰ΩúÊàê„Çí„Çπ„Ç≠„ÉÉ„Éó„Åó„Åæ„Åó„Åü');
        }
    }
    
    return manifest;
}

async function generateChecksum(filePath, algorithm) {
    const crypto = require('crypto');
    const fileBuffer = await fs.readFile(filePath);
    const hash = crypto.createHash(algorithm);
    hash.update(fileBuffer);
    return hash.digest('hex');
}

async function archiveOldBuilds() {
    console.log('üóÑÔ∏è  Âè§„ÅÑ„Éì„É´„Éâ„Çí„Ç¢„Éº„Ç´„Ç§„Éñ„Åó„Å¶„ÅÑ„Åæ„Åô...');
    
    const themesDir = path.join(config.distDir, 'themes');
    const files = await fs.readdir(themesDir);
    
    // „Çø„Ç§„É†„Çπ„Çø„É≥„Éó‰ªò„Åç„ÅÆZIP„Éï„Ç°„Ç§„É´„ÇíÂèñÂæó
    const themeFiles = files
        .filter(f => f.startsWith(config.themeName) && f.endsWith('.zip'))
        .filter(f => f !== `${config.themeName}_latest.zip`);
    
    // ÊúÄÊñ∞5‰ª∂„ÇíÊÆã„Åó„Å¶Âè§„ÅÑ„ÇÇ„ÅÆ„Çí„Ç¢„Éº„Ç´„Ç§„Éñ
    if (themeFiles.length > 5) {
        const sortedFiles = themeFiles.sort().reverse();
        const toArchive = sortedFiles.slice(5);
        
        for (const file of toArchive) {
            const sourcePath = path.join(themesDir, file);
            const archivePath = path.join(config.distDir, 'archives', file);
            await fs.move(sourcePath, archivePath);
            console.log(`  üìÅ „Ç¢„Éº„Ç´„Ç§„Éñ: ${file}`);
        }
    }
}

async function generateBuildReport(manifest) {
    console.log('üìä „Éì„É´„Éâ„É¨„Éù„Éº„Éà„ÇíÁîüÊàê„Åó„Å¶„ÅÑ„Åæ„Åô...');
    
    const report = `
# „Éì„É´„Éâ„É¨„Éù„Éº„Éà

## „Éì„É´„ÉâÊÉÖÂ†±
- **Êó•ÊôÇ**: ${new Date().toLocaleString('ja-JP')}
- **„Çø„Ç§„É†„Çπ„Çø„É≥„Éó**: ${config.timestamp}
- **„Éê„Éº„Ç∏„Éß„É≥**: ${manifest.theme.version}
- **Áí∞Â¢É**: ${manifest.build.environment}

## „Éï„Ç°„Ç§„É´ÊÉÖÂ†±
- **„Éï„Ç°„Ç§„É´Âêç**: ${manifest.build.files.theme}
- **„Çµ„Ç§„Ç∫**: ${manifest.build.files.sizeHuman}
- **MD5**: ${manifest.checksums.md5}
- **SHA256**: ${manifest.checksums.sha256}

## „Éá„Éó„É≠„Ç§ÊÉÖÂ†±
- **„Çø„Éº„Ç≤„ÉÉ„ÉàURL**: ${manifest.deployment.targetUrl}
- **„Ç§„É≥„Çπ„Éà„Éº„É´„Éë„Çπ**: ${manifest.deployment.targetPath}

## Ê¨°„ÅÆ„Çπ„ÉÜ„ÉÉ„Éó
1. \`dist/themes/${manifest.build.files.theme}\` „ÇíWordPress„Å´„Ç¢„ÉÉ„Éó„É≠„Éº„Éâ
2. ÁÆ°ÁêÜÁîªÈù¢„Åß„ÉÜ„Éº„Éû„ÇíÊúâÂäπÂåñ
3. Âãï‰ΩúÁ¢∫Ë™ç„ÇíÂÆüÊñΩ

## „Ç≥„Éû„É≥„Éâ‰æã
\`\`\`bash
# „Éá„Éó„É≠„Ç§
npm run deploy:wordpress

# ÊâãÂãï„Ç¢„ÉÉ„Éó„É≠„Éº„Éâ
scp dist/themes/${manifest.build.files.theme} user@server:/path/to/wordpress/wp-content/themes/
\`\`\`
`;
    
    const reportPath = path.join(config.distDir, 'builds', `build-report_${config.timestamp}.md`);
    await fs.writeFile(reportPath, report.trim());
    
    console.log('\n' + report);
}

async function main() {
    console.log('üöÄ WordPress „ÉÜ„Éº„Éû„Éì„É´„Éâ„ÇíÈñãÂßã„Åó„Åæ„Åô...\n');
    
    try {
        // 1. „Éá„Ç£„É¨„ÇØ„Éà„É™Ê∫ñÂÇô
        await ensureDirectories();
        
        // 2. ‰∏ÄÊôÇ„Éá„Ç£„É¨„ÇØ„Éà„É™„ÇØ„É™„Éº„É≥„Ç¢„ÉÉ„Éó
        await cleanTempDirectory();
        
        // 3. „ÉÜ„Éº„Éû„Éï„Ç°„Ç§„É´„Ç≥„Éî„Éº
        await copyThemeFiles();
        
        // 4. „Ç¢„Çª„ÉÉ„Éà„Éì„É´„Éâ
        await runAssetBuild();
        
        // 5. ZIP„Ç¢„Éº„Ç´„Ç§„Éñ‰ΩúÊàê
        const zipInfo = await createZipArchive();
        
        // 6. „Éì„É´„Éâ„Éû„Éã„Éï„Çß„Çπ„Éà‰ΩúÊàê
        const manifest = await createBuildManifest(zipInfo);
        
        // 7. Âè§„ÅÑ„Éì„É´„Éâ„Çí„Ç¢„Éº„Ç´„Ç§„Éñ
        await archiveOldBuilds();
        
        // 8. „Éì„É´„Éâ„É¨„Éù„Éº„ÉàÁîüÊàê
        await generateBuildReport(manifest);
        
        // 9. ‰∏ÄÊôÇ„Éá„Ç£„É¨„ÇØ„Éà„É™„ÇØ„É™„Éº„É≥„Ç¢„ÉÉ„Éó
        await cleanTempDirectory();
        
        console.log('\n‚ú® „Éì„É´„Éâ„ÅåÊ≠£Â∏∏„Å´ÂÆå‰∫Ü„Åó„Åæ„Åó„ÅüÔºÅ');
        console.log(`üì¶ Âá∫ÂäõÂÖà: dist/themes/${zipInfo.fileName}`);
        
    } catch (error) {
        console.error('‚ùå „Éì„É´„Éâ„Ç®„É©„Éº:', error.message);
        process.exit(1);
    }
}

// „É°„Ç§„É≥Âá¶ÁêÜ„ÇíÂÆüË°å
if (require.main === module) {
    main();
}

module.exports = { getTimestamp, config };